/* News card styling */
.news-card {
  padding: 10px;
  transition: transform 200ms ease, box-shadow 200ms ease;
}
.news-card .card {
  border-radius: 12px;
}

/* image */
.news-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  transition: transform 350ms ease;
}

/* source badge over image */
.source-badge {
  position: absolute;
  right: 10px;
  top: 10px;
  background: rgba(0,0,0,0.7);
  color: white;
  padding: 6px 10px;
  border-radius: 20px;
  font-size: 12px;
  z-index: 3;
  text-transform: capitalize;
}

/* hover lift */
.news-card:hover {
  transform: translateY(-6px);
}
.news-card:hover .news-img {
  transform: scale(1.04);
}

/* gradient button */
.btn-gradient {
  background: linear-gradient(90deg, #ff7a18 0%, #af002d 50%, #319197 100%);
  color: #fff;
  border: none;
  box-shadow: 0 6px 18px rgba(49, 145, 151, 0.14);
}
.btn-gradient:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(49, 145, 151, 0.18);
  color: #fff;
}

/* smaller adjustments */
.card-title {
  font-weight: 600;
  font-size: 1.03rem;
  margin-bottom: 6px;
}
.card-text {
  font-size: 0.92rem;
}

/* make layout denser on small screens */
@media (max-width: 576px) {
  .news-img {
    height: 160px;
  }
}

/* container tweak: nicer spacing for grid */
.news-grid {
  margin-top: 20px;
  margin-bottom: 40px;
}
